<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText"><text>返回</text></block>
			<block slot="content"><text> 我的积分记录 </text></block>
		</cu-custom>
		
			<view class="cu-list menu-avatar">
				<template v-for="(item,index) in items">
				<view class="cu-item">
					
					<view class="content">
						<view class="text-grey">积分+{{item.quantity}}</view>
						<view class="text-gray text-sm flex">
							<view class="text-cut">
								<text class="cuIcon-infofill text-red  margin-right-xs"></text>
								{{item.content}}
							</view> </view>
					</view>
					<view class="action">
						<view class="text-grey text-xs">{{item.creationTimeDateBeforeNow}}</view>
					
					</view>
				</view>
		</template>
			</view>
			
	</view>
</template>

<script>
	export default {
		data() {
			return {
				items:[]
			}
		},
		onShow(){
			this.GetMyIntegrationRecord();
		},
		methods: {
			//获取文章类型
			async GetMyIntegrationRecord() {
				let url = "/Components/WxBase/GetMyIntegrationRecord";
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {});
				if (success == true) {
					this.items = items;
				}
			},
		}
	}
</script>

